import { Component, OnInit } from '@angular/core';
import { FormBuilder, Validators } from '@angular/forms';
import { AlertController, IonItemSliding, Platform } from '@ionic/angular';
import { functionality } from 'src/Table/table';
import { FunctionalityService } from '../Service/Functionality.service';

@Component({
  selector: 'app-functionality',
  templateUrl: './functionality.page.html',
  styleUrls: ['./functionality.page.scss'],
})
export class FunctionalityPage implements OnInit {
   regform = this.fb.group({});
  functionalityId: string;
  editMode:boolean
  listOfFunctionality:functionality[];
  usePicker: boolean;
  constructor(private fb:FormBuilder,
     private functionalityService:FunctionalityService,
     private alertController:AlertController,
     private platform:Platform) { }

  ngOnInit() {
    this.regform = this.fb.group({
    SN:["",Validators.required],
    compName: ["",Validators.required],
    description:["",],
    remark: ["",]
    });
    if ((this.platform.is('mobile') && !this.platform.is('hybrid')) ||
    this.platform.is('desktop')
  ) {
    this.usePicker = true;
  }
    this.getFunctionality();
  }
  getFunctionality(){
    this.functionalityService.getAllFunctionality().subscribe(res=>{
      this.listOfFunctionality = res;
    })
  }
  saveFunactionality(){
  if(this.regform.valid){
    if(!this.functionalityId){
      this.functionalityService.create(this.regform.value);
      this.presentAlert() 
    }
    else{
      this.functionalityService.updateFunctionality(this.regform.value,this.functionalityId)
    }
    this.functionalityId = null;
    this.regform.reset();
  }
  else{
    this.ErrorAlert();
  }
}
Edit(functionlityObj:functionality,slide:IonItemSliding){
  this.editMode=true;
  this.functionalityId = functionlityObj.id;
  this.regform.get('SN').setValue(functionlityObj.SN);
  this.regform.get('compName').setValue(functionlityObj.compName);
  this.regform.get('description').setValue(functionlityObj.description);
  this.regform.get('remark').setValue(functionlityObj.remark);
  slide.close();
}
delete(func:functionality,slide:IonItemSliding){
  this.presentAlertConfirm(func);
  slide.close();
}
async presentAlertConfirm(functionality :functionality) {
  const alert = await this.alertController.create({
    header: 'Confirm!',
    message: '<strong>Are you sure you want delete?</strong>',
    buttons: [
      {
        text: 'Cancel',
        role: 'cancel',
        cssClass: 'secondary',
        handler: (blah) => {
          console.log('Confirm Cancel: blah');
        }
      }, {
        text: 'OK',
        handler: () => {
        this.functionalityService.removeFunctionality(functionality.id);
       this.regform.reset();
       this.functionalityId=null;
        }
      }
    ]
  });
 await alert.present();
}
async ErrorAlert() {
  const alert = await this.alertController.create({
    cssClass: 'my-custom-class',
    header: 'Error',
    // subHeader: 'Subtitle',
    message: 'Please Enter All field.',
    buttons: ['OK']
  });

  await alert.present();
}
async presentAlert() {
  const alert = await this.alertController.create({
    cssClass: 'my-custom-class',
    header: 'Account',
    // subHeader: 'Subtitle',
    message: 'Account Saved successfully.',
    buttons: ['OK']
  });

  await alert.present();
}
}
